
<template>
  <div class="NumberPapers2" >
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'NumberPapers2',
  data () {
    return {
      id: 'NumberPapers2'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
          animationDuration: 5000,
      dataset: [
        {
          source: [
            ['', 'kitti', 'flyingthings3D','semantickitti','nuscenes'],
            [2018, 230, 21,51,2], [2019, 369, 24,9,27], [2020, 456, 35,57,148], [2021, 524, 33,83,194], [2022, 325, 26,68,185]

          ]
        }
      ],
      title: {
        text: '',
        left: "center"
      },
      grid: {
        left: "1%",
        containLabel: true
      },
      legend: {
        "bottom": "10%"
      },
      tooltip: {
        order: 'valueDesc',
        trigger: 'axis',
      },
      xAxis: {
        name: "",
        type: 'category',
        // nameLocation: 'middle'
      },
      yAxis: {
        name: '',
        type: "value",
      },
      series: [
        {type: "bar"},
        {type: "bar"},
        {type: "bar"},
        {type: "bar"},

      ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .NumberPapers2 {
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
