<template>
  <article class="message is-link" id='chart-marketsize'>
    <div class="message-body">
    
    <br/>
    <br/>
    203010.6    </div>
  </article>
  <div class="MarketSize" id='chart-suanli'>
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'MarketSize',
  data () {
    return {
      id: 'MarketSize'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
         title: {
        text: "2020-2030",
        left: "center",
      },
      tooltip: {
        // trigger: "item",
        // formatter: "{a} <br/>{b} : {c} ({d}%)"
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        data: ["2021", "2021", "2022e","2023e", "2024e", "2025e","2026e", "2027e", "2028e","2029e","2030e"],
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: [1448, 3098, 5165,8943,16410,23331,34592,47156,62955,83205,105963],
          type: "line",
        },
      ],
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .MarketSize{
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
