}

.menu-label {
  font-size: 0.9rem !important;
  font-weight: bold;
  margin-bottom: 0.4rem !important;
  margin-top: 0.4rem !important;
}

.menu-item-active {
  color: white !important;
  box-shadow: $cyan 0 0 10px;

  &::before {
    background-color: $cyan;
    width: 100% !important;
  }
}


.menu-list a {
  position: relative;
  transition: all 0.7s ease;
  border-radius: 6px;
  overflow: hidden;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  margin-bottom: 0.2rem;
  font-size: 0.8rem;

  &::before {
    transition: all 0.7s ease;
    height: 100%;
    content: '';
    width: 0;
    opacity: 1;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
  }

  &:hover {
    color: white !important;
    box-shadow: $cyan 0 0 10px;
    background-color: $cyan !important;
  }
}
</style>