
<template>
  <div class="home" >
    <div class="Chart" :id='id'></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'Home',
  data () {
    return {
      id: 'HighestRouteCompletion'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
        title: {
        text: "Carla",
        left: "center",
        },
        tooltip: {
  
              formatter: function(param) {  
                          var value = param.value;  
                          return '<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 16px;padding-bottom: 7px;margin-bottom: 7px;"> ' + value[2] + '(' + value[1] + ')' +  
                              '</div>';  
                        }  
        },

        xAxis: {
            type: 'time',
            name: '',
            min: '2020-09-01',
            max: '2022-08-01',
        },
        yAxis: {
            type: 'value',
            name: '',
            
        },
        series: [
          {
            name: '',
            data:[
                      ['2020-10-01',
                          17.54,
                          "LBC"
                      ],
                      [
                          '2020-12-01',
                          46.97,
                          "MaRLn"
                      ],
                      [
                          '2021-04-01',
                          57.65,
                          "World on Rails"
                      ],
                      [
                          '2021-08-01',
                        61.85,
                          "GRIAD"
                      ],
                      [
                          '2021-09-01',
                          69.84,
                          "Transfuser"
                      ],
                      [
                          '2022-04-01',
                          94.459,
                          "LAV"
                      ],
                ],
            type: "line",
            symbolSize: 10,
          }
        ],
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .home {
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
