
<template>
  <div class="NumberPapers4">
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'NumberPapers4',
  data () {
    return {
      id: 'NumberPapers4'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
           animationDuration: 5000,
      dataset: [
        {
          source: [
            ['', 'DL', 'RL','AVS'],
            [2013, 37300, 34000,20000], [2014, 41700, 37100,21200], [2015,47900, 41200,21700], [2016, 57900, 46500,21600], [2017, 79000, 59200,23600],[2018,118000,84100,28400],[2019,163000,114000,33300],[2020,193000,133000,33000],[2021,232000,160000,34700],[2022,128000,87300,16200]

          ]
        }
      ],
      title: {
        text: ' DLRL',
        left: "center"
      },
      grid: {
        left: "1%",
        containLabel: true
      },
      legend: {
        "bottom": "10%"
      },
      tooltip: {
        order: 'valueDesc',
        trigger: 'axis',
      },
      xAxis: {
        name: "",
        type: 'category',
        // nameLocation: 'middle'
      },
      yAxis: {
        name: '',
        type: "value",
      },
      series: [
        {type: "bar"},
        {type: "bar"},
      ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .NumberPapers4{
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
