<template>
  <article class="message is-link">
    <div class="message-body">
    1000TOPS
    <br/>
    <br/>
    L2L3  </div>
  </article>
  <div class="SuanLi" id='chart-suanli'>
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'SuanLi',
  data () {
    return {
      id: 'SuanLi'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
           title: {
        text: "(TOPS)",
        left: "center",
      },
      tooltip: {
        // trigger: "item",
        // formatter: "{a} <br/>{b} : {c} ({d}%)"
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        data: ["L1_L3", "L4", "L5"],
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: [2.5, 24, 320],
          type: "line",
        },
      ],
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .SuanLi{
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
