<template>
  <div class="NumberPapers" >
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'NumberPapers',
  data () {
    return {
      id: 'NumberPapers'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
          animationDuration: 5000,
      dataset: [
        {
          source: [
            ['', 'CARLA', 'TORCS'],
            [2018, 20, 10], [2019, 73, 11], [2020, 131, 8], [2021, 197, 10], [2022, 147, 3]

          ]
        }
      ],
      title: {
        text: 'CARLATORCS',
        left: "center"
      },
      grid: {
        left: "1%",
        containLabel: true
      },
      legend: {
        "bottom": "10%"
      },
      tooltip: {
        order: 'valueDesc',
        trigger: 'axis',
      },
      xAxis: {
        name: "",
        type: 'category',
        // nameLocation: 'middle'
      },
      yAxis: {
        name: '',
        type: "value",
      },
      series: [
        {type: "bar"},
        {type: "bar"},
      ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .NumberPapers{
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
