<template>
  <div class="NumberPapers3">
    <div class="Chart" :id='id' ></div>
  </div>
</template>

<script>
// import echarts from 'echarts'
import * as echarts from 'echarts' // echartsinit udefined

export default {
  name: 'NumberPapers3',
  data () {
    return {
      id: 'NumberPapers3'
    }
  },
  mounted () {
    this.myChart()
  },
  methods: {
    myChart () {
      // 
      const myChart = echarts.init(document.getElementById(this.id))
      // 
      myChart.resize(
        {
          width: document.getElementById(this.id).offsetWidth,
          height: document.getElementById(this.id).offsetHeight
        }
      )
      // 
      var option = {
           animationDuration: 5000,
      dataset: [
        {
          source: [
            ['', 'RL&AVS', 'DL&AVS'],
            [2013, 192, 93], [2014, 175, 117], [2015,248, 224], [2016, 298, 378], [2017, 573, 900],[2018,1060,1950],[2019,1740,3150],[2020,2080,3850],[2021,2810,4870],[2022,1510,2600]

          ]
        }
      ],
      title: {
        text: 'RL&AVSDL&AVS',
        left: "center"
      },
      grid: {
        left: "1%",
        containLabel: true
      },
      legend: {
        "bottom": "10%"
      },
      tooltip: {
        order: 'valueDesc',
        trigger: 'axis',
      },
      xAxis: {
        name: "",
        type: 'category',
        // nameLocation: 'middle'
      },
      yAxis: {
        name: '',
        type: "value",
      },
      series: [
        {type: "bar"},
        {type: "bar"},
      ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
  .NumberPapers3{
    overflow: auto;
    .Chart {
      height: 400px;
    }
  }
</style>
